import axios from 'axios'
import React, { useEffect, useState, useSyncExternalStore } from 'react'
import { Link, useParams } from 'react-router-dom'

export const BuyMp = () =>{

    const [ producto , setProducto ] = useState([])
    const [ infoProduct , setInfoProduct ] = useState({
        titulo:'',
        precio:''
    })
    const [ formInput , setFormInput ] = useState({
        nombre:'',
        direccion:'',
        numero:'',
        cp:'',
        ciudad:'',
        provincia:'',
        piso:'',
        telefono:'',
        mail:'',
        indicaciones:'',
        titleProduct:'',
        priceproduct:''
    })
    const [ linkPago , setLinkPago ] = useState('')

    const {id} = useParams()

    useEffect(()=>{
        const peticionBuy = async() =>{
            await axios.get(`https://e-commercebackendtila.herokuapp.com/gpdloadproduct/detalle/${id}`)
            .then((resp)=>{
                setProducto(resp.data)
                setInfoProduct({
                    titulo:resp.data.titulo,
                    precio:resp.data.precio
                })
            })
            .catch(err=>{
                console.log(err)
            })
        }
        peticionBuy()
        },[])

    console.log(infoProduct)

    useEffect(()=>{
        const handleClick = async() =>{
            await axios.post('https://e-commercebackendtila.herokuapp.com/gpdloadproduct/pay' , {
                titulo:producto.titulo,
                precio:producto.precio
            })
            .then(resp=>{
                setLinkPago(resp.data.message.init_point)
            })
            .catch(err=>{
                console.log(err)
            })
        }

        handleClick()
    },[infoProduct])



    const handleChange = (e) =>{
        setFormInput({
            ...formInput,
            [e.target.name]:e.target.value,
            titleProduct:infoProduct.titulo,
            priceproduct:infoProduct.precio
        })

    }

    const handleSubmit = async() =>{
        await axios.post('http://localhost:27017/gpdloadproduct/datauserform' , formInput)
        .then(send=>{
            console.log('Enviado Correctamente: ', send )
        })
        .catch(err=>{
            console.log(err)
        })

        window.location.href = `${linkPago}`
    } 

    console.log(formInput)


    return(
        <div className='col-sm-12 container'>
            <div className='col-sm-6 m-auto'>
                <div>
                    <div>
                        <div className='col-sm-12 row text-center'>
                            <div className='col-sm-6'>
                                <h3>Producto</h3>
                            </div>
                            <div className='col-sm-6'>
                                <h3>Precio</h3>
                            </div>
                        </div>
                        <div className='col-sm-12 row'>
                            <div className='col-sm-6'>
                                <p>{infoProduct.titulo}</p>
                            </div>
                            <div className='col-sm-6 text-center'>
                                <p>{infoProduct.precio} $AR</p>
                            </div>
                        </div>
                    </div>
                    <div>
                        <form onSubmit={handleSubmit}>
                            <div className="form-floating mb-3">
                                <input type="text" className="form-control" id="floatingInput" placeholder="Nombre y Apellido" name='nombre' onChange={handleChange}/>
                                <label for="floatingInput">Nombre y Apellido</label>
                            </div>
                            <div className="form-floating mb-3">
                                <input type="text" className="form-control" id="floatingInput" placeholder="Direccion" name='direccion' onChange={handleChange}/>
                                <label for="floatingInput">Direccion</label>
                            </div>
                            <div className='col-sm-12 row'>
                                <div className='col-sm-4'>
                                    <div className="form-floating mb-3  m-auto">
                                        <input type="text" className="form-control" id="floatingInput" placeholder="Ej: 5014" name='numero' onChange={handleChange}/>
                                        <label for="floatingInput">Numero</label>
                                    </div>
                                </div>
                                <div className='col-sm-4'>
                                    <div className="form-floating mb-3">
                                        <input type="text" className="form-control" id="floatingInput" placeholder="C.P" name='cp' onChange={handleChange}/>
                                        <label for="floatingInput">Codigo Postal</label>
                                    </div>
                                </div>
                            </div>
                            <div className="form-floating mb-3">
                                <input type="text" className="form-control" id="floatingInput" placeholder="Ciudad" name='ciudad' onChange={handleChange}/>
                                <label for="floatingInput">Ciudad</label>
                            </div>
                            <div className="form-floating mb-3">
                                <input type="text" className="form-control" id="floatingInput" placeholder="Provincia" name='provincia' onChange={handleChange}/>
                                <label for="floatingInput">Provincia</label>
                            </div>
                            <div className="form-floating mb-3">
                                <input type="text" className="form-control" id="floatingInput" placeholder="Piso / Departamento" name='piso' onChange={handleChange}/>
                                <label for="floatingInput">Piso / Departamento</label>
                            </div>
                            <div>
                                <p>Contacto</p>
                            </div>
                            <div className="form-floating mb-3">
                                <input type="text" className="form-control" id="floatingInput" placeholder="Telefono de Contacto" name='telefono' onChange={handleChange}/>
                                <label for="floatingInput">Telefono de Contacto</label>
                            </div>
                            <div className="form-floating mb-3">
                                <input type="text" className="form-control" id="floatingInput" placeholder="Mail" name='mail' onChange={handleChange}/>
                                <label for="floatingInput">Mail</label>
                            </div>
                            <div className="form-floating mb-3">
                                <input type="text" className="form-control" id="floatingInput" placeholder="Indicaciones Adicionales" name='indicaciones' onChange={handleChange}/>
                                <label for="floatingInput">Indicaciones Adicionales</label>
                            </div>
                            <div className='col-sm-12'>
                                <div className='text-center container-button-buy'>
                                    <button type='submit'>Continuar</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}