import React from 'react'
import mp from '../img/mercadopago.png'
import maestro from '../img/maestro.svg'
import visa from '../img/visa.svg'
import mastercard from '../img/mastercard.svg'

export const MediosDePago = () =>{
    return(
        <div className='container-pagos-medios'>
            <div className='text-center mb-5'>
                <h2>Medios de pagos habilitado</h2>
            </div>
            <div>
                <div className='container-fluid col-sm-12 row medios-pago-haiblitado text-center'>
                    <div className='col-sm-3'>
                        <img src={mp} alt='mercadopago'/>
                    </div>
                    <div className='col-sm-3'>
                        <img src={maestro} alt='maestro'/>
                    </div>
                    <div className='col-sm-3 d-flex pt-4'>
                        <div className='w-100'>
                            <img src={visa} alt='visa'/>
                        </div>
                    </div>
                    <div className='col-sm-3'>
                        <img src={mastercard} className='mastercard'/>
                    </div>
                </div>
            </div>
            <div>
                <div className='text-center mb-5'>
                    <p>Aceptamos todas las tarjeta de credito y debito</p>
                </div>
            </div>
        </div>
    )
}