import React, { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import axios from 'axios'
import { Footer } from '../index/footer/footer'
import { TbTruckDelivery } from 'react-icons/tb';
import enviogratis from '../index/img/enviogratis.svg'

export const ProductDescription = () =>{

    const {id} = useParams()
    console.log(id)

    const [ producId , setProductId ] = useState([])


    /*------------------------------ PETICION SOBRE EL ID ------------------------------*/
    useEffect(()=>{
        const peticionId = async()=>{
        await axios.get(`https://e-commercebackendtila.herokuapp.com/gpdloadproduct/detalle/${id}`)
        .then(resp=>{
            setProductId(resp.data)
            console.log(resp.data)
        })
        .catch(err=>{
            console.log('Error en la peticion de ID: ', err)
        })    
    }
        peticionId()
    },[])

    console.log(producId)
    /*------------------------------ PETICION SOBRE EL ID ------------------------------*/
    /*
    <Link to={`/buyproduct/${id}`}>Comprar</Link>
    */

    return(
        <>
        <div className="container-banner-header">
            <div className="header-nav container m-auto">
                <ul className="col-sm-12 d-flex text-center nav-link">
                    <li className="col-sm-3"><Link to={'/categoria/billetera'} className='billetera'>Billetera</Link></li>
                    <li className="col-sm-3"><Link to={'/categoria/mochila'} className='mochila'>Mochilas</Link></li>
                    <li className="col-sm-3"><Link to={'/categoria/gorras'} className='gorra'>Gorras</Link></li>
                    <li className="col-sm-3"><Link to={'/categoria/contacto'} className='contacto'>Contacto</Link></li>
                </ul>
            </div>
        </div>
        <div className='container description-product d-flex'>
            <div className='row col-sm-12'>
                <div className='col-sm-6'>
                    <div className='text-center img-product-description'>
                        <img src={producId.url}/>
                    </div>
                </div>
                <div className='col-sm-6'>
                    <div>
                        <div>
                            <h2>{producId.titulo}</h2>
                        </div>
                        <div>
                            <h3>AR$ {producId.precio}</h3>
                        </div>
                        <div className='d-flex'>
                            <div className='delivery-container-icono'>
                            <p><TbTruckDelivery className='icono-delivery'/> Envio Gratis <strong> de 3 a 5 dias habiles</strong></p>
                            </div>
                        </div>
                        <div className='btn-container-comprar d-flex'>
                            <Link to={`/buyproduct/${id}`} className='btn-comprar'>Comprar</Link>
                        </div>
                        <div>
                            <div>
                                <div>
                                    <h4>Descripcion:</h4>
                                </div>
                                <div>
                                    <p>{producId.descripcion}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <Footer/>
        </>
    )
}