import React from "react";
import { Link } from "react-router-dom";
import bannerlogo from '../img/logo-banner.svg'

export const Header = () =>{
    return(
        <div className="container-banner-header">
            <div className="header-nav container m-auto">
                <ul className="col-sm-12 d-flex text-center nav-link">
                    <li className="col-sm-3"><Link to={'/categoria/billetera'} className='billetera'>Billetera</Link></li>
                    <li className="col-sm-3"><Link to={'/categoria/mochila'} className='mochila'>Mochilas</Link></li>
                    <li className="col-sm-3"><Link to={'/categoria/gorras'} className='gorra'>Gorras</Link></li>
                    <li className="col-sm-3"><Link to={'/categoria/contacto'} className='contacto'>Contacto</Link></li>
                </ul>
            </div>
            <div className="foto-logo">
                <div className="w-100 text-center">
                    <img src={bannerlogo}/>
                </div>
            </div>
        </div>
    )
}