import React, { useEffect, useState }  from "react";
import axios from 'axios'
import { Link } from 'react-router-dom'

export const TiendaIndex = () =>{

    const [ productos , setProductos ] = useState([])

    
    const peticionProductos = async() =>{
        await axios.get('https://e-commercebackendtila.herokuapp.com/gpdloadproduct')
        .then(res=>{
            setProductos(res.data)
        })
        .catch(err=>{
            console.log(err)
        })
    }

    useEffect(()=>{
        peticionProductos()
    },[])
    

    return(
        <div className="body-tienda pb-5">
            <div>
                <div className="text-center pt-5 pb-5 titulo-body-tienda">
                    <h2>Nuestra Tienda</h2>
                </div>
                <div className="container">
                    <div className="col-sm-12 row">
                        {
                            productos.map(product=>(
                                <div className="col-sm-4 card-container" key={product._id}>
                                    <div className="card-producto-index text-center">
                                        <div className="product-img text-center">
                                            <img src={`${product.url}`} />
                                        </div>
                                        <div className="card-title">
                                            <h3 className="ellipsis-text-title">{product.titulo}</h3>
                                        </div>
                                        <div className="text-center mb-5">
                                            <h3 id="precio-card">${product.precio}</h3>
                                        </div>
                                        <div className="button-card-index text-center mb-5">
                                            <Link to={`/producto/detalle/${product._id}`} id='button-vermas'>Ver Mas</Link>
                                        </div>    
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                </div>
            </div>
        </div>
    )
}