import React from "react";
import provincia from '../img/provinciabsas.svg';


export const EnviosIndex = () =>{
    return(
        <div className="container-envio-bsas pb-5">
            <div>
                <div className="txt-envio-index text-center">
                    <p>
                        Envios <strong>SIN CARGO</strong> de nuestro productos <br/>dentro de la provincia de Buenos Aires
                    </p>
                </div>
                <div>
                    <div className="text-center">
                        <img src={provincia} alt='provincia bs as'/>
                    </div>
                </div>
            </div>
        </div>
    )
}